@Component({
  selector: 'elipsesLoading',
  styles: [`
    .elipses-loading-one {
        opacity: 0;
        -webkit-animation: elipses-loading-dot 1.3s infinite;
        -webkit-animation-delay: 0.0s;
        animation: elipses-loading-dot 1.3s infinite;
        animation-delay: 0.0s;
    }

    .elipses-loading-two {
        opacity: 0;
        -webkit-animation: elipses-loading-dot 1.3s infinite;
        -webkit-animation-delay: 0.2s;
          animation: elipses-loading-dot 1.3s infinite;
          animation-delay: 0.2s;
    }

    .elipses-loading-three {
        opacity: 0;
        -webkit-animation: elipses-loading-dot 1.3s infinite;
        -webkit-animation-delay: 0.3s;
        animation: elipses-loading-dot 1.3s infinite;
        animation-delay: 0.3s;
    }

    @-webkit-keyframes elipses-loading-dot {
          0% { opacity: 0; }
        50% { opacity: 0; }
        100% { opacity: 1; }
    }

    @keyframes elipses-loading-dot {
          0% { opacity: 0; }
        50% { opacity: 0; }
        100% { opacity: 1; }
    }
  `],
  template: `
  <span class="elipses-loading-one">.</span><span class="elipses-loading-two">.</span><span class="elipses-loading-three">.</span>
  `
})
class ElipsesLoadingComponent {}