@Service('panel')
@Inject('$mdPanel')
class PanelService {
  private panel: any;
  constructor(private $mdPanel: angular.material.IPanelService) {}
  show(template: string, locals: any) {
    let position = this.$mdPanel.newPanelPosition().absolute().center();
    let config :angular.material.IPanelConfig = {
      attachTo: angular.element(document.body),
      controller: function () {},
      controllerAs: 'vm',
      disableParentScroll: true,
      template: template,
      hasBackdrop: false,
      panelClass: 'demo-dialog-example',
      position: position,
      trapFocus: false,
      zIndex: 88,
      clickOutsideToClose: true,
      escapeToClose: true,
      focusOnOpen: true,
      locals: locals
    };
    return this.$mdPanel.open(config)
      .then(panel => this.panel = panel)
  }
  close() {
    if (this.panel) this.panel.close()
  }
}
