@Service('ardorPendingTransactions')
@Inject('$q', 'http', 'settings')
class ArdorPendingTransactionsService {

  public pending: {[address:string]:Array<{txId:string,time:number, fullHash: string}>} = {}
  private listeners: Array<(removed?: boolean)=>void> = []

  constructor(public $q: angular.IQService,
              private http: HttpService,
              private settings: SettingsService) {
    this.readLocalStorage()
  }

  readLocalStorage() {
    this.pending = {}
    for (let i=0; i<window.localStorage.length; i++) {
      let key = window.localStorage.key(i)
      if (key.startsWith('ardrPendingTxn:')) {
        let parts = key.split(':'), addr = parts[1],  txId = parts[2], time = parseInt(parts[3]), fullHash = parts[4]
        this.pending[addr] = this.pending[addr] || []
        this.pending[addr].push({txId:txId, time: time, fullHash: fullHash});
      }
    }
  }

  add(address:string, txId:string, timestamp: number, fullHash: string) {
    window.localStorage.setItem(`ardrPendingTxn:${address}:${txId}:${timestamp}:${fullHash}`, "1")
    this.readLocalStorage()
    this.notifyListeners(false)
  }

  remove(address:string, txId:string, timestamp: number, fullHash: string) {
    window.localStorage.removeItem(`ardrPendingTxn:${address}:${txId}:${timestamp}:${fullHash}`)
    this.readLocalStorage()
    this.notifyListeners(true)
  }

  addListener(func: (removed?: boolean)=>void) {
    this.removeListener(func)
    this.listeners.push(func)
  }

  removeListener(func: (removed?: boolean)=>void) {
    this.listeners = this.listeners.filter(fn => fn !== func)
  }

  notifyListeners(removed: boolean) {
    this.listeners.forEach(fn => fn(removed))
  }
}